import { ChangeEvent, FormEvent, Fragment, useState } from "react";
import { Link } from "react-router-dom";
import { userService } from "../../services";

export const ForgotPassword = () => {
    const [inputs, setInputs] = useState({
        email: ''
    });

    const [res, setRes] = useState(false);
    const [loading, setLoading] = useState(false);
    const [submitted, setSubmitted] = useState(false);

    const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setInputs((inputs) => ({ ...inputs, [name]: value }));
    };

    const { email } = inputs;

    const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        setSubmitted(true);
        if (email) {
            setLoading(true);
            const response = await userService.FotgotPassword(email);
            if (response.status === 200) {
                setRes(true);
                setLoading(false);
            }
        }
    };

    return (
        <div className="container">
            <div className="row justify-content-center">
                <div className="col-xl-10 col-lg-12 col-md-9">
                    <div className="card o-hidden border-0 shadow-lg my-5">
                        <div className="card-body p-0">
                            <div className="row">
                                <div className="col-lg-6 d-none d-lg-block bg-password-image" />
                                <div className="col-lg-6">
                                    <div className="p-5">
                                        {
                                            res === false ?
                                                <Fragment>
                                                    <div className="text-center">
                                                        <h1 className="h4 text-gray-900 mb-2">Bn qun mt khu ca mnh?</h1>
                                                        <p className="mb-4">
                                                            Chng ti hiu iu g ang xy ra vi bn. Hy nhp email ca bn vo bn di
                                                            v n khi phc mt khu, bn s ly li c quyn truy cp ti khon ca chnh mnh!!!
                                                        </p>
                                                    </div>
                                                    <form className="user" onSubmit={handleSubmit}>
                                                        <div className="form-group">
                                                            <input type="email" name='email' className={'form-control form-control-user ' + (submitted && !email ? 'is-invalid' : '')} onChange={handleChange} id="exampleInputEmail" aria-describedby="emailHelp" placeholder="example@gmail.com" />
                                                            {submitted && !email && (
                                                                <div className='invalid-feedback' style={{ display: "flex", justifyContent: "center" }}>
                                                                    Email l bt buc
                                                                </div>
                                                            )}
                                                        </div>
                                                        <button className="btn btn-primary btn-user btn-block">
                                                            {loading && (
                                                                <span className='spinner-border spinner-border-sm mr-1'></span>
                                                            )}
                                                            Khi phc mt khu
                                                        </button>
                                                    </form>
                                                </Fragment> :
                                                <div className="text-center">
                                                    <h1 className="h4 text-gray-900 mb-2">Kim tra hm th  tip tc?</h1>
                                                    <p className="mb-4">
                                                         hon thnh qu trnh ly li mt khu, vui lng kim tra hm th ca bn v tip tc!!!
                                                    </p>
                                                </div>
                                        }
                                        <hr />
                                        <div className="text-center">
                                            <Link className="small" to='/login'>
                                                Bn  c ti khon? Hy ng nhp!
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}